<template>
  <div id="cssTheme">
    <div>
      <el-radio-group v-model="themeType" @change="handleTheme">
        <el-radio-button label="lighter"></el-radio-button>
        <el-radio-button label="dark"></el-radio-button>
        <el-radio-button label="red"></el-radio-button>
      </el-radio-group>
    </div>
    <div class="box cssvar-theme" >
      css
    </div>
    <test />
  </div>
</template>

<script>
import cssVars from 'css-vars-ponyfill';
import theme from '@/theme/cssVarsPonyfill';
import Test from '@/components/test.vue';

const INITIALIZED_THEME = 'lighter';

export default {
  name: 'App',
  components: {
    Test,
  },
  data() {
    return {
      themeType: INITIALIZED_THEME,
    };
  },
  created() {
    this.initTheme(INITIALIZED_THEME);
  },
  methods: {
    initTheme(initializedTheme) {
      this.handleTheme(initializedTheme);
    },
    handleTheme(themeType) {
      document.documentElement.setAttribute('data-theme', themeType);
      cssVars({
        // watch: true, // <link><style>hrefponyfill
        variables: theme[themeType], // variables /
        onlyLegacy: false, // false  csscss  true csscsscss
      });
    },
  },
};
</script>

<style>
#cssTheme {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.cssvar-theme {
  color: var(--text-color);
  background-color: var(--bg-color);
}

</style>
